<template>
  <div>
    <van-nav-bar
      title="核选项目列表详情"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-cell-group>
      <van-cell title="项目代码" :value="chkProjectDetails.number" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目名称" :value="chkProjectDetails.name" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="申报单位" :value="chkProjectDetails.declare" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="管理单位" :value="chkProjectDetails.schedule" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目所在地" :value="chkProjectDetails.city" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="总投资" :value="chkProjectDetails.pri" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="项目规模及主要内容" :label="chkProjectDetails.text" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="审批部门" :value="chkProjectDetails.unit" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="联系电话" :value="chkProjectDetails.pom" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="备案申报日期" :value="chkProjectDetails.dateDay" />
    </van-cell-group>
    <van-cell-group>
      <van-cell title="复核申报日期" :value="chkProjectDetails.review" />
    </van-cell-group>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      chkProjectDetails: []
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.$axios.get(`/chkProjectDetails/${this.id}`).then(res => {
      this.chkProjectDetails = res.data;
      console.log(this.chkProjectDetails);
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
